const get_star_display = (floor_tin_gone_display, time_gone, time_chi, star) => {
    // 
    let start_gong = 0


    if (time_gone == "") {
        let gone = ""
        gak_time_gone.forEach((g) => {
            if (g['gak'] == time_chi)
                gone = g['gone']
        })


        floor_tin_gone_display.forEach((tg, idx) => { if (tg['primary'] == gone) start_gong = idx })
    }
    else
        floor_tin_gone_display.forEach((tg, idx) => { if (tg['primary'] == time_gone) start_gong = idx })

    if (start_gong == 8)
        start_gong = 5
    let star_seq_array = []
    // 
    if (star_seq.indexOf(star) != start_gong) {
        let shift_cnb = start_gong > star_seq.indexOf(star) ? start_gong - star_seq.indexOf(star) : star_seq.indexOf(star) - start_gong
        // 

        star_seq_array = start_gong > star_seq.indexOf(star) ? shiftRight(star_seq, shift_cnb) : shiftLeft(star_seq, shift_cnb)
    }
    else {
        star_seq_array.push(...star_seq)
    }
    // 
    return star_seq_array
}

