import logo from './logo.svg';
import packageJson from '../package.json';
import './App.css';
import './css/main.css';
import 'bootstrap/dist/css/bootstrap.css';
import 'bootstrap/dist/js/bootstrap.js';
import { version_info } from './js/version_info.js'
import Accordion from 'react-bootstrap/Accordion';
import Modal from 'react-bootstrap/Modal';
import Alert from 'react-bootstrap/Alert';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import GridBoard from './Component/GridBoard';
import Notes from './Component/Notes'
import lunisolar from 'lunisolar'
import React, { useState, useEffect, useRef } from 'react'
import ReactLoading from 'react-loading';
import { tin_gone_10_str, floor12_str, getSolarTerm, door_seq, star_seq, sun_seq, tin_gone_seq, removeItemOnce, gong_seq, nearestDates, findSameGone, find_good_gok, goodGokFiltering,savedGokFiltering } from './js/main.js'
import { find_solar_terms, find_current_solar_terms, find_solar_terms_offline, find_current_solar_terms_offline, roundToNearestMinute } from './js/solarTerms.js'

function App() {
  const yearMin = 1900
  const yearMax = 2099
  // const [dateInput, setDateInput] = useState(new Date())
  const [d, setD] = useState(lunisolar(new Date()))
  const [yinyang, setYinyang] = useState(1)
  const [char8, setChar8] = useState(
    d.toDate().getFullYear() < yearMin || d.toDate().getFullYear() > yearMax ?
      {
        year: "",
        month: "",
        day: "",
        time: ""
      }
      :
      {
        year: d.format('cY cM cD cH').split(" ")[0],
        month: d.format('cY cM cD cH').split(" ")[1],
        day: d.format('cY cM cD cH').split(" ")[2],
        time: d.format('cY cM cD cH').split(" ")[3]
      })

  let originalDataSetObj = {
    display_floor_tin_gone_array: [],
    display_tin_tin_gone_array: [],
    display_sun_array: [],
    display_floor_sun_array: [],
    display_star_array: [],
    display_door_array: [],
    yan_gone_array: []
  }

  const [haziDate, setHaziDate] = useState(new Date())
  const [dongziDate, setDongziDate] = useState(new Date())
  const [previousZit, setPreviousZit] = useState(new Date().getTime())
  const [currentZit, setCurrentZit] = useState(new Date().getTime())
  const [dateUpdated, setDateUpdated] = useState(false)
  const [yearUpdated, setYearUpdated] = useState(false)
  const [fan_or_fok, setFan_or_fok] = useState(false)
  const [spin, setSpin] = useState(false)
  const [spinLeftValue, setSpinLeftValue] = useState(0)
  const [spinRightValue, setSpinRightValue] = useState(0)
  const [targets, setTargets] = useState([])
  const [showTargets, setShowTargets] = useState(false)
  const [goodBadGoneColor, setGoodBadGoneColor] = useState(false)
  const [selectedGone, setSelectedGone] = useState(-1)
  const [gakgokDesc, setGakgokDesc] = useState([])
  const [showModal, setShowModal] = useState(false);
  const [yearGone, setYearGone] = useState(-1)
  const [monthGone, setMonthGone] = useState(-1)
  const [dayGone, setDayGone] = useState(-1)
  const [timeGone, setTimeGone] = useState(-1)
  const [savedTime, setSavedTime] = useState(JSON.parse(localStorage.getItem("savedTime")) || [])
  const [savedRemarks, setSavedRemarks] = useState(JSON.parse(localStorage.getItem("savedRemarks")) || [])
  const [key, setKey] = useState('main')
  const [visibleAlert, setVisibleAlert] = useState(false)
  const [importAlert, setImportAlert] = useState(false)
  const [removeAllAlert, setRemoveAllAlert] = useState(false)
  const [showVersionInfo, setShowVersionInfo] = useState(true)
  const [importError, setImportError] = useState(false)
  const [searchError, setSearchError] = useState(false)
  const [sameGone, setSameGone] = useState(new Set())
  const [goodGok, setGoodGok] = useState([])
  const [validInput, setValidInput] = useState(true)
  const [filterKeyWords, setFilterKeyWords] = useState("")
  const [filterSavedKeyWords, setFilterSavedKeyWords] = useState("")

  const handleClose = () => setShowModal(false);
  const handleShow = () => setShowModal(true);

  const dateInput = useRef("")
  const remarksRef = useRef(null)
  const filterRef = useRef(null)
  const fileRef = useRef(null)
  const dayCntsRef = useRef(null)
  const yearGoneInput = useRef(null)
  const monthGoneInput = useRef(null)
  const dayGoneInput = useRef(null)
  const timeGoneInput = useRef(null)
  const [goneDates, setGoneDates] = useState(nearestDates(d))

  const fetchGoodGok = async () => {
    let _input = dayCntsRef.current.value
    if (_input > 0 && _input <= 365) {
      let d = lunisolar(new Date())
      const response = await find_good_gok(d, 12 * _input)
      const _goodGok = response
      setGoodGok(_goodGok);
    }
    else {
      setSearchError(true)
      dayCntsRef.current.focus()
    }
  }

  const importButton = (e) => {
    fileRef.current.click();
  }

  const showImportModal = (e) => setImportAlert(true)
  const hideImportModal = (e) => setImportAlert(false)
  const showRemoveAllModal = (e) => setRemoveAllAlert(true)
  const hideRemoveAllModal = (e) => setRemoveAllAlert(false)

  const removmAllButton = () => {
    setSavedTime([])
    setSavedRemarks([])
    localStorage.setItem("savedTime", JSON.stringify([]));
    localStorage.setItem("savedRemarks", JSON.stringify([]));
    hideRemoveAllModal()
  }

  const exportData = () => {
    let objArray = []
    savedTime.forEach((t, idx) => {
      objArray.push({
        time: t,
        remarks: savedRemarks[idx]
      })
    });

    const jsonString = `data:text/json;chatset=utf-8,${encodeURIComponent(
      JSON.stringify(objArray)
    )}`;
    const link = document.createElement("a");
    link.href = jsonString;
    link.download = "saved.json";

    link.click();
  };

  const exportGoodData = () => {
    let objArray = []
    goodGokFiltering(goodGok, filterKeyWords).forEach((t) => {
      let sang_oh = ""
      if (t.sang_ohs.length > 0) {
        t.sang_ohs.forEach(s => sang_oh += `${s} `)
        sang_oh += " "
      }


      objArray.push({
        time: t.time,
        remarks: sang_oh + t.remarks
      })
    });

    const jsonString = `data:text/json;chatset=utf-8,${encodeURIComponent(
      JSON.stringify(objArray)
    )}`;
    const link = document.createElement("a");
    link.href = jsonString;
    link.download = ".json";

    link.click();
  };

  const importData = (e) => {
    const fileReader = new FileReader();
    fileReader.readAsText(e.target.files[0], "UTF-8");
    fileReader.onload = e => {
      try {
        let jsonArray = JSON.parse(e.target.result)

        let t_savedTime = []
        let t_savedRemarks = []

        jsonArray.forEach(obj => {
          let t = obj.time
          let r = obj.remarks

          t_savedTime.push(t)
          t_savedRemarks.push(r)
        })

        if (t_savedTime.length > 0) {
          setSavedTime(t_savedTime)
          setSavedRemarks(t_savedRemarks)
          localStorage.setItem("savedTime", JSON.stringify(t_savedTime));
          localStorage.setItem("savedRemarks", JSON.stringify(t_savedRemarks));
        }
      }
      catch (e) {
        setImportError(true)
      }
    };

    hideImportModal()
  };

  const saveTimeAlert = () => {
    setVisibleAlert(true)
    setTimeout(() => {
      setVisibleAlert(false)
    }, 2000);
  }

  const saveTime = () => {
    let t = dateInput.current.value
    let r = remarksRef.current.value
    if (savedTime.indexOf(t) < 0) {
      setSavedTime([...savedTime, t])
      setSavedRemarks([...savedRemarks, r === '' ? "" : r])
      saveTimeAlert()
      localStorage.setItem("savedTime", JSON.stringify([...savedTime, t]));
      localStorage.setItem("savedRemarks", JSON.stringify([...savedRemarks, r === '' ? "" : r]));
    }
    else {
      savedRemarks[savedTime.indexOf(t)] = r === '' ? "" : r
      setSavedRemarks(savedRemarks)
      saveTimeAlert()
      localStorage.setItem("savedTime", JSON.stringify(savedTime));
      localStorage.setItem("savedRemarks", JSON.stringify(savedRemarks));
    }
  }

  const removeTime = (t) => {
    let temp_array = []
    let temp_remarks_array = []
    if (savedTime.length > 1) {
      let idx = savedTime.indexOf(t)
      temp_array = removeItemOnce(savedTime, t)
      temp_remarks_array = removeItemOnce(savedRemarks, savedRemarks[idx])
      setSavedTime([...temp_array])
      setSavedRemarks([...temp_remarks_array])
      localStorage.setItem("savedTime", JSON.stringify([...savedTime]));
      localStorage.setItem("savedRemarks", JSON.stringify([...savedRemarks]));
    }
    else {
      setSavedTime([])
      setSavedRemarks([])
      localStorage.setItem("savedTime", JSON.stringify([]));
      localStorage.setItem("savedRemarks", JSON.stringify([]));
    }

  }

  const loadTime = (t, r) => {
    dateInput.current.value = t
    remarksRef.current.value = r
    setD(lunisolar(new Date(t)))
    updateDateStr()

    setKey('main')
  }

  const updateGakGok = (gakgok) => {
    // console.log(`gakgok=${JSON.stringify(gakgok)}`);
    setSelectedGone(gakgok.seq)
    setGakgokDesc(gakgok.gakgok)
    handleShow()
  }

  const initOriginalGridDataSet = (dataSetObj) => {

    originalDataSetObj = dataSetObj
  }

  const showHideTargets = (event) => {
    setShowTargets(!showTargets)
  }

  const goneColor = (event) => {
    setGoodBadGoneColor(!goodBadGoneColor)
  }

  const updateTargets = (event) => {
    if (targets.indexOf(event.target.text) > -1) {
      let temp_array = []
      if (targets.length > 1) {
        temp_array = removeItemOnce(targets, event.target.text)
        setTargets([...targets, temp_array])
      }
      else
        setTargets([])
    }
    else {
      setTargets([...targets, event.target.text])
    }
    /*
    if (event.target.checked)
      setTargets([...targets, event.target.value])
    else {
      let temp_array = []
      if (targets.length > 1) {
        temp_array = removeItemOnce(targets, event.target.value)
        setTargets(...temp_array)
      }
      else
        setTargets([])
    }
    */
  }

  const clearTargets = (event) => {
    setTargets([])

  }

  // yyyy-mm-ddTHH:MM
  let year = "" + d.toDate().getFullYear()
  let month = "" + (d.toDate().getMonth() + 1)
  month = month.padStart(2, '0')
  let day = "" + d.toDate().getDate()
  day = day.padStart(2, '0')
  let hour = "" + d.toDate().getHours()
  hour = hour.padStart(2, '0')
  let min = "" + d.toDate().getMinutes()
  min = min.padStart(2, '0')

  // const [dateTimeStr, setDateTimeStr] = useState()



  // var yinyang = 0

  const resetGones = () => {
    setYearGone(-1)
    setMonthGone(-1)
    setDayGone(-1)
    setTimeGone(-1)
    yearGoneInput.current.value = -1
    monthGoneInput.current.value = -1
    dayGoneInput.current.value = -1
    timeGoneInput.current.value = -1
  }

  const updateDateStr = () => {
    setSelectedGone(-1)
    setGakgokDesc([])
    setDateUpdated(false)
    resetGones()

    year = "" + d.toDate().getFullYear()
    month = "" + (d.toDate().getMonth() + 1)
    month = month.padStart(2, '0')
    day = "" + d.toDate().getDate()
    day = day.padStart(2, '0')
    hour = "" + d.toDate().getHours()
    hour = hour.padStart(2, '0')
    min = "" + d.toDate().getMinutes()
    min = min.padStart(2, '0')
  }

  const inputChange = (event) => {
    if (!isNaN(new Date(event.target.value))) {
      setD(lunisolar(new Date(event.target.value)))
      updateDateStr()
    }
    remarksRef.current.value = ""
  }

  const addDayClick = (event) => {
    let date = d.toDate()
    date.setDate(date.getDate() + 1)
    setD(lunisolar(new Date(date)))

    updateDateStr()
    remarksRef.current.value = ""

  }

  const reduceDayClick = (event) => {
    let date = d.toDate()
    date.setDate(date.getDate() - 1)
    setD(lunisolar(new Date(date)))

    updateDateStr()
    remarksRef.current.value = ""

  }

  const addHourClick = (event) => {
    let date = d.toDate()
    if (date.getHours() % 2 == 0) {
      date.setHours(date.getHours() + 1)
    }
    else
      date.setHours(date.getHours() + 2)

    setD(lunisolar(new Date(date)))

    updateDateStr()
    remarksRef.current.value = ""

  }

  const reduceHourClick = (event) => {
    let date = d.toDate()
    if (date.getHours() % 2 == 0) {
      date.setHours(date.getHours() - 3)
    }
    else
      date.setHours(date.getHours() - 2)

    setD(lunisolar(new Date(date)))

    updateDateStr()
    remarksRef.current.value = ""

  }

  const resetNowClick = (event) => {
    setD(lunisolar(new Date()))
    updateDateStr()
    remarksRef.current.value = ""
  }

  const originalClick = (event) => {
    setSpin(false)
    setSpinLeftValue(0)
    setSpinRightValue(0)
    setSelectedGone(-1)
    setGakgokDesc([])
  }

  const spinLeft = (event) => {
    setSpin(true)
    setSpinRightValue(0)

    if (spinLeftValue + 1 >= 8)
      setSpinLeftValue(0)
    else
      setSpinLeftValue(spinLeftValue + 1)

    setSelectedGone(-1)
    setGakgokDesc([])
  }

  const spinRight = (event) => {
    setSpin(true)
    setSpinLeftValue(0)

    if (spinRightValue + 1 >= 8)
      setSpinRightValue(0)
    else
      setSpinRightValue(spinRightValue + 1)

    setSelectedGone(-1)
    setGakgokDesc([])
  }


  useEffect(() => {
    // let haziDate = getSolarTerm(d.toDate().getFullYear(), 11);
    // let currentZit = getSolarTerm(d.toDate().getFullYear(), (d.toDate().getMonth()) * 2)
    const fetchHazi = async () => {
      const response = await find_solar_terms_offline("", d.toDate().getFullYear())
      // .then((result) => haziDate = new Date(result))
      const _haziDate = response
      setHaziDate(_haziDate)
    }

    const fetchDongzi = async () => {
      const response = await find_solar_terms_offline("", d.toDate().getFullYear())
      // .then((result) => haziDate = new Date(result))
      const _dongziDate = response
      setDongziDate(_dongziDate)
    }

    const fetchCurrentZit = async (datetime) => {
      const response = await find_current_solar_terms_offline(d.toDate().getMonth(), d.toDate().getFullYear())
      // .then((result) => currentZit = new Date(result))
      let first = response['first']
      let second = response['second']
      // if (roundToNearestMinute(datetime).getTime() >= roundToNearestMinute(first).getTime()) {

      //   setCurrentZit(first.getTime())
      // }

      setPreviousZit(first.getTime())
      // if (roundToNearestMinute(datetime).getTime() >= roundToNearestMinute(second).getTime()) {
      setCurrentZit(second.getTime())
      // }
      // 
    }



    let char8_splits = {}
    if (d.toDate().getFullYear() >= yearMin && d.toDate().getFullYear() < yearMax) {
      fetchHazi()
      fetchDongzi()
      fetchCurrentZit(d.toDate())

      // let hazi = lunisolar.SolarTerm.findDate(d.toDate().getFullYear(), 9, {})
      // let haziDate = new Date(hazi[0], hazi[1], hazi[2])
      // let donzi = lunisolar.SolarTerm.findDate(d.toDate().getFullYear(), 21, {})
      // let donziDate = new Date(donzi[0], donzi[1], donzi[2])
      if (d.toDate().getTime() < haziDate) {
        setYinyang(1)
      }
      else if (d.toDate().getTime() >= dongziDate) {
        setYinyang(1)
      }
      else if (d.toDate().getTime() >= haziDate && d.toDate().getTime() < dongziDate) {
        setYinyang(0)
      }
      char8_splits = d.format('cY cM cD cH').split(" ")

      if (
        roundToNearestMinute(d.toDate()).getTime() <= roundToNearestMinute(new Date(currentZit)).getTime()
      ) {
        let t_c_splits = char8_splits[1].split("")
        let year_splits = char8_splits[0].split("")


        if (t_c_splits.length == 2) {
          let gone_idx = tin_gone_10_str.indexOf(t_c_splits[0])
          let chi_idx = floor12_str.indexOf(t_c_splits[1])

          let new_gone = t_c_splits[0]
          let new_chi = t_c_splits[1]

          let year_gone_idx = tin_gone_10_str.indexOf(year_splits[0])
          let year_chi_idx = floor12_str.indexOf(year_splits[1])

          let new_year_gone = year_splits[0]
          let new_year_chi = year_splits[1]

          // if(month === "01"){
          let currentZitDate = new Date(currentZit)


          // console.log(`currentZitDate=${currentZitDate}`);
          // console.log(`d.toDate()=${d.toDate()}`);
          // console.log(`d.toDate().getDate() = ${d.toDate().getDate()}`);
          // console.log(`currentZitDate.getDate() = ${currentZitDate.getDate()}`);
          // console.log(`d.toDate().getHours() = ${d.toDate().getHours()}`);
          // console.log(`d.toDate().getDate() == currentZitDate.getDate() = ${d.toDate().getDate() == currentZitDate.getDate()}`);
          // console.log(`d.toDate().getHours() <= currentZitDate.getHours() = ${d.toDate().getHours() <= currentZitDate.getHours()}`);
          // console.log(`d.toDate().getMinutes() <= currentZitDate.getMinutes() = ${d.toDate().getMinutes() <= currentZitDate.getMinutes()}`);
          if (
            (
              d.toDate().getDate() == currentZitDate.getDate() - 1
              &&
              d.toDate().getHours() >= 23
            )
            ||
            (
              d.toDate().getDate() == currentZitDate.getDate()
              &&
              (
                d.toDate().getHours() < currentZitDate.getHours() ||
                (d.toDate().getHours() == currentZitDate.getHours() && d.toDate().getMinutes() <= currentZitDate.getMinutes())
              )
            )
          ) {
            if (gone_idx == 0) {
              // if (roundToNearestMinute(d.toDate()).getTime() < previousZit)
              new_gone = tin_gone_10_str[tin_gone_10_str.length - 1]
            }
            else {
              if (d.lunar.month - 1 != gone_idx)
                new_gone = tin_gone_10_str[gone_idx - 1]
            }

            if (chi_idx == 0) {
              // if (roundToNearestMinute(d.toDate()).getTime() < previousZit)
              new_chi = floor12_str[floor12_str.length - 1]
            }
            else {
              if (d.lunar.month - 1 != gone_idx)
                new_chi = floor12_str[chi_idx - 1]
            }
            // console.log(`month=${month}`);
            if (month === "02") {
              // console.log(`year_gone_idx=${year_gone_idx} year_chi_idx=${year_chi_idx}`);
              if (year_gone_idx == 0) {
                new_year_gone = tin_gone_10_str[tin_gone_10_str.length - 1]
              }
              else {
                if (11 != year_gone_idx)
                  new_year_gone = tin_gone_10_str[year_gone_idx - 1]
              }

              if (year_chi_idx == 0) {
                new_year_chi = floor12_str[floor12_str.length - 1]
              }
              else {
                if (11 != year_gone_idx)
                  new_year_chi = floor12_str[year_chi_idx - 1]
              }
            }
            // console.log(`new_year_gone=${new_year_gone} new_year_chi=${new_year_chi}`);
          }
          // }
          // else {
          //     // if(oldMonth !== char8_splits[1]){

          //         if (gone_idx == 0) {
          //           // if (roundToNearestMinute(d.toDate()).getTime() < previousZit)
          //             new_gone = tin_gone_10_str[tin_gone_10_str.length - 1]
          //         }
          //         else {
          //           if (d.lunar.month - 1 != gone_idx)
          //             new_gone = tin_gone_10_str[gone_idx - 1]
          //         }

          //         if (chi_idx == 0) {
          //           // if (roundToNearestMinute(d.toDate()).getTime() < previousZit)
          //             new_chi = floor12_str[floor12_str.length - 1]
          //         }
          //         else { 
          //           if (d.lunar.month - 1 != gone_idx)
          //             new_chi = floor12_str[chi_idx - 1]
          //         }


          //       // }

          //   }
          char8_splits[0] = new_year_gone + new_year_chi
          char8_splits[1] = new_gone + new_chi

          setChar8({ year: char8_splits[0], month: char8_splits[1], day: char8_splits[2], time: char8_splits[3] })
        }
      }
      else {
        setChar8({ year: char8_splits[0], month: char8_splits[1], day: char8_splits[2], time: char8_splits[3] })
      }
      setDateUpdated(true)
      setGoneDates(nearestDates(d))

      setSameGone(findSameGone(char8, d, yinyang))
    }
    else {
      setValidInput(false)
      setChar8({ year: "", month: "", day: "", time: "" })
    }


  }, [d.toDate().getTime(), char8.year, char8.month, char8.day, char8.time, haziDate.getTime(), currentZit, dateUpdated, spinLeftValue, spinRightValue, targets, showTargets, yearGone, monthGone, dayGone, timeGone])

  return (
    <div className="App">
      <meta name="viewport" content="width=device-width, initial-scale=1.0, maximum-scale=1.0, user-scalable=no"></meta>
      <Tabs
        defaultActiveKey="main"
        activeKey={key}
        onSelect={(k) => setKey(k)}
        id="justify-tab-example"
        className="mb-3 tabPage"
        justify
      >
        <Tab eventKey="main" title="">
          <div className='top_message_div'>
            <Alert variant='warning' dismissible>Excel</Alert>
          </div>
          <div className='header_div'>
            <Container>
              <Row>
                <Col xs={3} ><label></label></Col>
                <Col><input className='form-control' ref={remarksRef} type='text' placeholder='///etc.' /></Col>
              </Row>
              <Row>
                <Col xs={3}><label> </label></Col>
                <Col><input ref={dateInput} onChange={(e) => inputChange(e)} className='form-control' type="datetime-local" name="dateInput"
                  value={`${year}-${month}-${day}T${hour}:${min}`}
                  min={`${yearMin}-01-01T00:00`}
                  max={`${yearMax}-12-31T23:59`}
                /></Col>
              </Row>
              <Row>
                <Col>
                  <div className='button_row'>
                    <input type='button' className='btn btn-secondary' onClick={(e) => resetNowClick(e)} value="" />
                    <input type='button' className='btn btn-secondary' onClick={(e) => originalClick(e)} value="" />
                    <input type='button' className='btn btn-secondary' onClick={(e) => reduceDayClick(e)} value="" />
                    <input type='button' className='btn btn-secondary' onClick={(e) => addDayClick(e)} value="" />
                    <input type='button' className='btn btn-secondary' onClick={(e) => reduceHourClick(e)} value="" />
                    <input type='button' className='btn btn-secondary' onClick={(e) => addHourClick(e)} value="" />
                    <input type='button' className='btn btn-secondary' onClick={(e) => spinLeft(e)} value={` [${spinLeftValue}]`} />
                    <input type='button' className='btn btn-secondary' onClick={(e) => spinRight(e)} value={` [${spinRightValue}]`} />
                    <input type='button' className={`btn ${showTargets ? 'btn-warning' : 'btn-dark'}`} onClick={showHideTargets} value="" />
                    <input type='button' className={`btn ${goodBadGoneColor ? 'btn-warning' : 'btn-dark'}`} onClick={goneColor} value="" />
                  </div>
                </Col>
                <Col>
                  <div className='char8_day'>
                    <span className={`char8 ${sameGone.has('year') ? 'same_year' : ''}`}> {char8['year']}</span>
                    <span className={`char8 ${sameGone.has('month') ? 'same_month' : ''}`}> {char8['month']}</span>
                    <span className={`char8 ${sameGone.has('day') ? 'same_day' : ''}`}> {char8['day']}</span>
                    <span className={`char8 ${sameGone.has('time') ? 'same_time' : ''}`}> {char8['time']}</span>
                  </div>
                  <div className='char8_day'>
                    <span className='char8 year_gone'> <select ref={yearGoneInput} onChange={(e) => setYearGone(e.target.value)}><option value="-1"></option>{gong_seq.map(g => <option key={`year_${g}`} value={g}>{g}</option>)}</select></span>
                    <span className='char8 month_gone'> <select ref={monthGoneInput} onChange={(e) => setMonthGone(e.target.value)}><option value="-1"></option>{gong_seq.map(g => <option key={`month_${g}`} value={g}>{g}</option>)}</select></span>
                    <span className='char8 day_gone'> <select ref={dayGoneInput} onChange={(e) => setDayGone(e.target.value)}><option value="-1"></option>{gong_seq.map(g => <option key={`day_${g}`} value={g}>{g}</option>)}</select></span>
                    <span className='char8 time_gone'> <select ref={timeGoneInput} onChange={(e) => setTimeGone(e.target.value)}><option value="-1"></option>{gong_seq.map(g => <option key={`time_${g}`} value={g}>{g}</option>)}</select></span>
                    <br />
                    <input type='button' className='btn btn-success' onClick={(e) => saveTime()} value={``} />
                  </div>
                </Col>
              </Row>
            </Container>
            <div className='targets_div alert alert-warning' style={{ display: showTargets ? '' : 'none' }}>
              <div className='targets_row'>
                {
                  door_seq.map(d => <span className='targets_span'> <a href="#!" key={`door_targets_${d}`} onClick={(e) => updateTargets(e)} className={`btn ${targets.indexOf(d) > -1 ? 'btn-warning' : 'btn-dark'}`}>{d}</a></span>)
                }
              </div>
              <div className='targets_row'>
                {
                  star_seq.map(d => <span className='targets_span'> <a href="#!" key={`star_targets_${d}`} onClick={(e) => updateTargets(e)} className={`btn ${targets.indexOf(d) > -1 ? 'btn-warning' : 'btn-dark'}`}>{d}</a></span>)
                }
              </div>
              <div className='targets_row'>
                {
                  sun_seq.map(d => <span className='targets_span'> <a href="#!" key={`sun_targets_${d}`} onClick={(e) => updateTargets(e)} className={`btn ${targets.indexOf(d) > -1 ? 'btn-warning' : 'btn-dark'}`}>{d}</a></span>)
                }
              </div>
              <div className='targets_row'>
                {
                  tin_gone_seq.map(d => <span className='targets_span'> <a href="#!" key={`tin_gone_targets_${d}`} onClick={(e) => updateTargets(e)} className={`btn ${targets.indexOf(d) > -1 ? 'btn-warning' : 'btn-dark'}`}>{d}</a></span>)
                }
              </div>
            </div>
          </div>
          <div className='top_message_div'>
            <Alert variant='success' show={visibleAlert} ></Alert>
            <Alert variant='danger' show={!validInput} onClose={() => setValidInput(true)} dismissible >{`${yearMin} - ${yearMax}`}</Alert>
          </div>
          {
            dateUpdated ?
              <div className='header_div'>
                <div className='grid_board_div'>
                  <GridBoard
                    year_chi={char8['year'].charAt(1)}
                    month_chi={char8['month'].charAt(1)}
                    day={d.toDate().getHours() >= 23 ? lunisolar(new Date(d.toDate().getTime() + 3600000)).lunar.day : d.lunar.day}
                    year_gone={char8['year'].charAt(0)}
                    month_gone={char8['month'].charAt(0)}
                    day_gone={char8['day'].charAt(0)}
                    time_gone={d.char8.hour.stem.toString()}
                    time_chi={d.char8.hour.branch.toString()}
                    yinyang={yinyang}
                    initOriginalGridDataSet={initOriginalGridDataSet}
                    originalDataSetObj={originalDataSetObj}
                    spin={spin}
                    spinLeftValue={spinLeftValue}
                    spinRightValue={spinRightValue}
                    targets={targets}
                    goodBadGoneColor={goodBadGoneColor}
                    updateGakGok={updateGakGok}
                    yearGone={yearGone}
                    monthGone={monthGone}
                    dayGone={dayGone}
                    timeGone={timeGone}
                    sameGone={sameGone}
                  />
                </div>
                <Modal show={showModal} onHide={handleClose} centered>
                  <Modal.Header closeButton>
                    <Modal.Title>[{selectedGone === -1 ? "" : selectedGone}]</Modal.Title>
                  </Modal.Header>
                  <Modal.Body>
                    <Alert variant='primary'>
                      <span>{selectedGone === -1 ? "" : ''}</span>
                      <Alert.Heading></Alert.Heading>
                      <div className='gakgok'>
                        {gakgokDesc.map((g) => {
                          return (
                            <Row>
                              <Col xs={4}>
                                <span className='gakgok_title'>{g.title}<br /> [{g.primary_tin_gone}+{g.secondary_tin_gone}]</span>
                              </Col>
                              <Col>
                                <span className='gakgok_desc'>{g.desc}</span>
                              </Col>
                            </Row>
                          )
                        })}
                      </div>
                    </Alert>
                    <Alert variant='warning'>
                      <span>{selectedGone === -1 ? "" : ''}</span>
                      <Alert.Heading>(30)</Alert.Heading>
                      <div className='gakgok'>
                        {goneDates.map((g) => {
                          if (g.gone === selectedGone)
                            return (
                              <Row>
                                <Col xs={3}>
                                  <span className='gakgok_title'>{g.lunar_day_chi}({g.yin_yang})({g.animal})</span>
                                </Col>
                                <Col>
                                  <Alert variant={`${g.best_before_date && !g.past ? 'success' : 'danger'}`}>
                                    {g.date}
                                  </Alert>
                                </Col>
                              </Row>
                            )
                        })}
                      </div>
                    </Alert>
                    {
                      d.toDate().getFullYear() < yearMin || d.toDate().getFullYear() > yearMax
                        ?
                        <Container></Container>
                        :
                        <Alert variant='success'>
                          <span>{selectedGone === -1 ? "" : ''}</span>
                          <Alert.Heading></Alert.Heading>
                          <div className='gakgok'>
                            <Row>
                              <Col>
                                <span>{d.toDate().toISOString().split('T')[0].replaceAll('-', '/')} - {new Date(d.toDate().setDate(d.toDate().getDate() + 8)).toISOString().split('T')[0].replaceAll('-', '/')}</span>
                                {/* <span>{d.format('YYYY/MM/DD')} - {d.add(8, 'd').format('YYYY/MM/DD')}</span> */}
                              </Col>
                            </Row>
                          </div>
                        </Alert>
                    }

                  </Modal.Body>
                </Modal>
                <Modal show={importAlert} onHide={hideImportModal} centered>
                  <Modal.Header closeButton>
                    <Modal.Title></Modal.Title>
                  </Modal.Header>
                  <Modal.Body>
                    <div className='alert alert-warning'>
                      <span><br /></span>
                    </div>
                  </Modal.Body>
                  <Modal.Footer>
                    <Button variant='success' onClick={(e) => importButton()}></Button>
                    <Button variant='danger' onClick={hideImportModal}></Button>
                  </Modal.Footer>
                </Modal>
                <Modal show={removeAllAlert} onHide={hideRemoveAllModal} centered>
                  <Modal.Header closeButton>
                    <Modal.Title></Modal.Title>
                  </Modal.Header>
                  <Modal.Body>
                    <div className='alert alert-danger'>
                      <span><br /></span>
                    </div>
                  </Modal.Body>
                  <Modal.Footer>
                    <Button variant='success' onClick={(e) => removmAllButton()}></Button>
                    <Button variant='danger' onClick={hideRemoveAllModal}></Button>
                  </Modal.Footer>
                </Modal>
                <div className='notes_div'>
                  <Accordion defaultActiveKey={['0']} alwaysOpen>
                    <Accordion.Item eventKey="0">
                      <Accordion.Header></Accordion.Header>
                      <Accordion.Body>
                        <Notes
                          year_chi={char8['year'].charAt(1)}
                          month_chi={char8['month'].charAt(1)}
                          day={d.toDate().getHours() >= 23 ? lunisolar(new Date(d.toDate().getTime() + 3600000)).lunar.day : d.lunar.day}
                          time_gone={d.char8.hour.stem.toString()}
                          time_chi={d.char8.hour.branch.toString()}
                          yinyang={yinyang}
                          initOriginalGridDataSet={initOriginalGridDataSet}
                          originalDataSetObj={originalDataSetObj}
                          spin={spin}
                          spinLeftValue={spinLeftValue}
                          spinRightValue={spinRightValue}
                        />
                      </Accordion.Body>
                    </Accordion.Item>
                  </Accordion>
                </div>
              </div>
              :
              <Container></Container>
            // <div className='grid_board_div'>
            //   <ReactLoading type="bubbles" color="#000" height={'200px'} width={'200px'} />
            // </div>
          }
          <Container>
            <Row>
              <Col>
                <span>  v{packageJson.version}</span>
              </Col>
            </Row>
            <Row>
              <Col>
                <span>Copyright  2024 elvischan91 all rights reserved</span>
              </Col>
            </Row>
          </Container>
          <Modal show={showVersionInfo} onHide={(e) => setShowVersionInfo(false)} centered>
            <Modal.Header closeButton></Modal.Header>
            <Modal.Body>
              <Alert variant='success'>
                {
                  version_info.map(v => {
                    return <div>{v.version_number} {v.updates}</div>
                  })
                }
              </Alert>
            </Modal.Body>
          </Modal>
        </Tab>
        <Tab eventKey="saved" title="">
          <Alert variant='danger' show={importError} onClose={() => setImportError(false)} dismissible>
            JSON
          </Alert>
          <Container>
            <Row className='savedButtonRow'>
              <Col xs={3}>
                <Button variant='success' onClick={(e) => exportData()}></Button>
              </Col>
              <Col>
                <span>{savedGokFiltering(savedTime,filterSavedKeyWords,savedRemarks).length}</span>
              </Col>
              <Col xs={3}>
                <input ref={fileRef} type='file' accept="application/JSON" onChange={(e) => importData(e)} style={{ display: 'none' }} />
                <Button variant='primary' onClick={(e) => showImportModal(e)}></Button>
              </Col>
            </Row>
            {
              savedTime.length == 0 ?
                "" :
                <Row className='searchRow'>
                  <Col>
                    <input type='text' className='form-control' onChange={(e) => setFilterSavedKeyWords(e.target.value)} placeholder='...'></input>
                  </Col>
                </Row>
            }
            <Row className='tableHeaderRow'>
              <Col xs={2} className=''></Col>
              <Col xs={5} className=''></Col>
              <Col xs={3} className=''></Col>
              <Col xs={2} className=''></Col>
            </Row>
            {
              savedTime.length == 0 ?
                <Row><Col></Col></Row>
                :
                savedGokFiltering(savedTime,filterSavedKeyWords,savedRemarks).map((t, idx) => {
                  return (
                    <Row className={`savedTimeRow ${idx % 2 === 0 ? 'even_row' : 'odd_row'}`}>
                      <Col xs={2}>
                        <Button variant="danger" onClick={(e) => removeTime(t)}></Button>
                      </Col>
                      <Col xs={5} className='savedTime'>{t.remarks}</Col>
                      <Col xs={3} className='savedTime'><span>{`${t.time.substr(0, 10)} ${t.time.substr(11, 6)}`}</span>
                      </Col>
                      <Col xs={2}><Button variant="primary" onClick={(e) => loadTime(t.time, t.remarks)}></Button></Col>
                    </Row>
                  )
                })
            }
            <Row className='savedButtonRow'>
              <Col>
                <Button variant='danger' onClick={(e) => setRemoveAllAlert(true)}></Button>
              </Col>
            </Row>

          </Container>

        </Tab>
        <Tab eventKey="goodGok" title="">
          <Alert variant='danger' show={searchError} onClose={() => setSearchError(false)} dismissible>
            1-365
          </Alert>
          <Alert variant='danger' show>
            <br />(,)
          </Alert>
          <Container fluid>
            <Row className='savedButtonRow'>
              <Col xs={2}>
                <span></span>
              </Col>
              <Col xs={2}>
                <input ref={dayCntsRef} className='form-control' type='number' min={1} max={365} step={1} required></input>
              </Col>
              <Col xs={5}>
                <span></span>
              </Col>
              <Col xs={3}>
                <Button variant='primary' onClick={(e) => fetchGoodGok()}></Button>
              </Col>
            </Row>
            <Row className='savedButtonRow'>
              <Col xs={3}>
                <Button variant='success' onClick={(e) => exportGoodData()}></Button>
              </Col>
              <Col>
                <span>{goodGokFiltering(goodGok, filterKeyWords).length}</span>
              </Col>
              <Col xs={4}>
                <Button variant='danger' onClick={(e) => setGoodGok([])}></Button>
              </Col>
            </Row>
            {
              goodGok.length == 0 ?
                "" :
                <Row className='searchRow'>
                  <Col>
                    <input type='text' className='form-control' onChange={(e) => setFilterKeyWords(e.target.value)} placeholder='...(:) (2:) ()'></input>
                  </Col>
                </Row>
            }
            <Row className='tableHeaderRow'>
              <Col xs={2} className=''></Col>
              <Col xs={2} className=''></Col>
              <Col xs={2} className=''></Col>
              <Col xs={4} className=''><span></span></Col>
              <Col xs={1} className=''></Col>
            </Row>
            {
              goodGokFiltering(goodGok, filterKeyWords).length == 0 ?
                <Row><Col></Col></Row>
                :
                goodGokFiltering(goodGok, filterKeyWords).map((t, idx) => {
                  return (
                    <Row className={`tableBodyRow ${idx % 2 === 0 ? 'even_row' : 'odd_row'}`}>
                      <Col xs={2} className='original_gong'><span>{t.remarks}</span></Col>
                      <Col xs={2} className='sangs'>{t.sang_ohs.length > 0 ? t.sang_ohs.map((s) => { return (<div className='sang_oh'>{s}</div>) }) : ""}</Col>
                      <Col xs={2} className=''><span className='year_gone'>{t.cyear}</span><br /><span className='month_gone'>{t.cmonth}</span><br /><span className='day_gone'>{t.cday}</span><br /><span className='time_gone'>{t.ctime}</span></Col>
                      <Col xs={4} className='savedTime'><span>{`${t.time.substr(0, 10)} ${t.time.substr(11, 6)}`}</span>
                      </Col>
                      <Col xs={1}><Button variant="primary" onClick={(e) => loadTime(t.time, t.remarks)}></Button></Col>
                    </Row>

                  )
                })
            }

          </Container>

        </Tab>

      </Tabs>



    </div >
  );
}

export default App;
