import React from 'react'

export default function Grid(props) {
  const chi_int_str = ['','','','','','','','','']
  const chi_int = [1,2,3,4,5,6,7,8,9]

  return (
    <div className='grid' >
        <span></span>
        <span>{props.yinyang == 0 ? "":""}</span>
        <span>{chi_int_str[chi_int.indexOf(props.gok)]}</span>
        <span></span>

        <span></span>
        <span>{props.chun_chi}</span>
        <span>{props.chun_gone}</span>
        <span></span>

        <span></span>
        <span>{props.time_gone}</span>
        <span>{props.time_chi}</span>
        <span></span>

        <span>{props.door}</span>
        <span>{props.star}</span>
        <span></span>
        <span>{props.floor_tin_gone}</span>
    </div>
  )
}
